import React from 'react';

export default (props) =>{
    let slen = props.snakeDots.length;
    let direction = props.direction;
    let classname = "snake-head "+direction;
    return (
        <div> 
            {
            
            props.snakeDots.map((dot,i)=>
            {
                const style={
                    left:`${dot[0]}%`,
                    top:`${dot[1]}%`,
                }
                if(i===(slen-1)){
                    return (<div className={classname} key={i} style={style}> </div>);
                }
                return (<div className="snake-dot" key={i} style={style}> </div>);
            })
            }
        </div>
    )
}