import React, { Component } from "react";
import PropTypes from "prop-types";
import SpeechRecognition from "react-speech-recognition";
import Snake from './Snake';
import Food from './Food';

const propTypes = {
  // Props injected by SpeechRecognition
  transcript: PropTypes.string,
  recognition: PropTypes.object,
  resetTranscript: PropTypes.func,
  interimTranscript:PropTypes.string
}

const initialState=[
  [0,0],
  [2,0],
  [4,0]
];

const getRandomFoodDot=()=>{
    //should not be point on snake body .  implement after learning HOOKs
    let min=6;
    let max=92;
    var x = Math.floor((Math.random()* (max-min+1) + min)/2)*2;
    var y = Math.floor((Math.random()* (max-min+1) + min)/2)*2;
    var dot= [x,y];
    return dot;
  };
const checkClash=(arr, dot) =>{
  //console.log(arr, dot);
  return arr.some(row => (row.includes(dot[0]) && row.includes(dot[1])));
}
var timer=0;

class Dictaphone extends Component {

  
  
  componentWillMount() {
    const { recognition } = this.props
   
    recognition.lang = 'en-IN'
    
  }

  state={
    snakeDots:initialState,
    speed:300,
    direction:'RIGHT',
    FoodDot: getRandomFoodDot(),
    speech:'none'
  }

  componentDidMount(){
      let speed=this.state.speed;
      clearInterval(timer);
      timer = setInterval(this.moveSnake,speed);

      document.onkeydown = this.onkeyDown;
     
  }
  componentDidUpdate(){
    let speed=this.state.speed;
    clearInterval(timer);
    timer = setInterval(this.moveSnake,speed);
    this.checkOutofBorders();
    //this.checkifCollapsed();
    this.checkIfEat();
  }

  onkeyDown=(e)=>{
    e = e || window.event;
    let vspeed= this.state.speed;
    let cDirection = this.state.direction;
    switch(e.keyCode){
        case 37:
          if(cDirection!='RIGHT')
            this.setState({direction:'LEFT'});
          break;
        case 38:
          if(cDirection!='DOWN')
            this.setState({direction:'UP'})
          break;
        case 39:
          if(cDirection!='LEFT')
            this.setState({direction:'RIGHT'})
          break;
        case 40:
          if(cDirection!='UP')
            this.setState({direction:'DOWN'})
          break;
        case 65:
          this.setState({
            speed : vspeed-50
          })
          clearInterval(timer);
          timer = setInterval(this.moveSnake,vspeed);
         
          break;
        case 83:
          this.setState({
              speed : vspeed+50
            })
          clearInterval(timer);
          timer = setInterval(this.moveSnake,vspeed);
          
          break;

    }
  }

  moveSnake=()=>{
      let dots= [...this.state.snakeDots];
      let head = dots[dots.length-1];

      switch(this.state.direction){
        case 'RIGHT':
          head = [head[0]+2,head[1]];
          break;
        case 'LEFT':
          head = [head[0]-2,head[1]];
          break;
        case 'UP':
          head = [head[0],head[1]-2];
          break;
        case 'DOWN':
          head = [head[0],head[1]+2];
          break;
      }
      dots.push(head);
      dots.shift();
      this.setState(
        {snakeDots:dots}
      )
  }

  checkifCollapsed=()=>{
      let snake =[...this.state.snakeDots];
      let head= snake[snake.length-1];
      let food=this.state.FoodDot;
      snake.pop();
      
      snake.forEach(dot=>{
        if( (head[0]==dot[0] && head[1]==dot[1]) && !(dot[0]==food[0] && dot[0]==food[1]) ){
          alert("Game Over!!! Score : "+this.state.snakeDots.length);
        }
      })

  }
  checkOutofBorders=()=>{
    let head= this.state.snakeDots[this.state.snakeDots.length-1];
    if( (head[0]<0 || head[0]>99 ) || (head[1]<0 || head[1]>99 )){
      alert("Game Over!!! Score : "+ (this.state.snakeDots.length-3));
      this.setState(
        {snakeDots:initialState,
          speed:200,
          direction:'RIGHT',
          FoodDot: getRandomFoodDot()}
      )
    }
  }

  checkIfEat(){
    let head= this.state.snakeDots[this.state.snakeDots.length-1];
    let head2=this.state.snakeDots[0];
    let food= this.state.FoodDot;
    /*
     if((head[0]==food[0] && head[0]==food[1])||
      head2[0]==food[0] && head2[0]==food[1]){
       */
    if((head[0]==food[0] && head[1]==food[1])){
        this.setState(
          {
            FoodDot:getRandomFoodDot()
          })  
        this.enlargeSnake();
        this.increaseSpeed();
        
    }
  }
  enlargeSnake(){
    let newSnake=[...this.state.snakeDots];
    newSnake.unshift(this.state.FoodDot);
    this.setState({
      snakeDots:newSnake
    })
  }

  increaseSpeed(){
    let vspeed= this.state.speed;
      if(vspeed>10){
        this.setState({
          speed : vspeed-10
        })
      }
    clearInterval(timer);
    timer = setInterval(this.moveSnake,vspeed);
  }
  
 
 
  
  render() {
    const { transcript } = this.props
    const {resetTranscript} = this.props
    const {interimTranscript} = this.props
    
    let trans = transcript.toString().toLowerCase()  //transcript
    //console.log(interimTranscript.toString());
    //console.log(trans.toString());
   
    if (transcript.toString().length>0){
      if(this.state.speech!=transcript ){
        this.setState({speech:transcript.toString()});
      }
    }
    if(trans.includes('left')|| trans.includes('best')|| trans.includes("ft") || trans.startsWith("l") || trans=='l'){
      this.setState({direction:'LEFT'});
      resetTranscript();
    }
    if(trans.includes('down')|| trans.includes("n")|| trans.startsWith("d") || trans=='d'|| trans=='b'){
      this.setState({direction:'DOWN'});
      resetTranscript();
    }
    if(trans.includes('up') || trans.includes('aap') || trans.includes('ab') || trans.endsWith("p") ||  trans=='u'){
      this.setState({direction:'UP'});
      resetTranscript();
    }
    if(trans.includes('right') || trans.includes('write') || trans.startsWith("r")||trans.endsWith('ht') ||  trans=='r'){
      this.setState({direction:'RIGHT'});
      resetTranscript();
    }



    return (
      <div>
        <div className="scoreBoard"> Score - {this.state.snakeDots.length-3}</div>
        <div className="scoreBoard"> Current Direction - {this.state.direction}<br/>
        <span>Speech Recognised - {this.state.speech}</span>
        </div>
        <div className="gameArena">
          <Snake snakeDots={this.state.snakeDots} direction={this.state.direction}/>
          <Food FoodDot={this.state.FoodDot}></Food>
        </div>
      </div>
    )
  }
}

Dictaphone.propTypes = propTypes

export default SpeechRecognition(Dictaphone)